import React from 'react';
import '../css/Home.css';
import { Link } from 'react-router-dom';
import TileOneImage from '../Assets/Images/BGImage.jpg'

const Blogs = () => {
	return (
		<div className="home col-lg-12">
			<div className="col-lg-12 food-type">
				<div className="col-lg-12 food-header">
					<div className="col-lg-6 category"><h5>Categories</h5></div>
					<div className="col-lg-6 see-all"><h5><Link to= "/egapp/menu">See all</Link></h5></div>
				</div>
				<hr className="category-hr"/>
				<div className="col-lg-12 col-sm-12 col-xs-12 col-md-12 card-main">
					<div className="card card-mob col-lg-6 col-xs-3 col-md-6 col-sm-3" style={{width: `18rem`}}>
						<img className="card-img-top" src={TileOneImage} alt="Card image cap" />
						<div className="card-body">
							<p className="card-text"><b>Grab your Lunch!</b></p>
						</div>
					</div>
					<div className="card card-mob col-lg-6 col-xs-3 col-md-6 col-sm-3" style={{width: `18rem`}}>
						<img className="card-img-top" src={TileOneImage} alt="Card image cap" />
						<div className="card-body">
							<p className="card-text"><b>Grab your Snacks!</b></p>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default Blogs;
